<template>
  <div>
    <input type="file" @change="onFileUpload" accept="image/*">
    <div v-if="essay">
      <h3></h3>
      <p>{{ essay }}</p>
    </div>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs'
import * as mobilenet from '@tensorflow-models/mobilenet'

export default {
  name: 'AI',
  data () {
    return {
      model: null,
      essay: ''
    }
  },
  async mounted () {
    await tf.ready() //  TensorFlow
    this.model = await mobilenet.load()
  },
  methods: {
    async onFileUpload (event) {
      const file = event.target.files[0]
      if (!file) return

      // 1. 
      const img = await this.loadImage(file)
      const predictions = await this.model.classify(img)

      // 2. 3
      const top3 = predictions.slice(0, 3)
        .map(pred => ({
          name: pred.className.replace(/,/g, ''), // 
          prob: Math.round(pred.probability * 100)
        }))

      // 3. 
      this.generateEssay(top3)
    },

    loadImage (file) {
      return new Promise((resolve) => {
        const reader = new FileReader()
        reader.onload = (e) => {
          const img = new Image()
          img.src = e.target.result
          img.onload = () => resolve(img)
        }
        reader.readAsDataURL(file)
      })
    },

    generateEssay (categories) {
      // 
      this.essay = `${categories[0].name}${categories[1].name}${categories[2].name}\
        ${this.getAdjective(categories[0].prob)}\
        ${categories[0].name}${categories[0].prob}%\
        ${this.getRandomTheme()}`
    },

    // 
    getAdjective (prob) {
      return prob > 90 ? '' : prob > 70 ? '' : ''
    },

    // 
    getRandomTheme () {
      const themes = ['', '', '', '']
      return themes[Math.floor(Math.random() * themes.length)]
    }
  }
}
</script>
